import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from 'recharts';
import { Download, FileText, Printer } from 'lucide-react';

const Reportes = () => {
    // Datos falsos para el grfico de barras
    const dataFinanciera = [
        { mes: 'Jul', ingresos: 4000, egresos: 2400 },
        { mes: 'Ago', ingresos: 3000, egresos: 1398 },
        { mes: 'Sep', ingresos: 2000, egresos: 9800 },
        { mes: 'Oct', ingresos: 2780, egresos: 3908 },
        { mes: 'Nov', ingresos: 1890, egresos: 4800 },
        { mes: 'Dic', ingresos: 2390, egresos: 3800 },
    ];

    return (
        <Container fluid className="p-4 dashboard-container">
            <div className="d-flex justify-content-between align-items-center mb-4">
                <div>
                    <h2 className="fw-bold text-white">Reportes Gerenciales</h2>
                    <p className="text-muted-dark">Estadsticas financieras y operativas</p>
                </div>
                <div className="d-flex gap-2">
                    <button className="btn btn-outline-light d-flex align-items-center gap-2">
                        <Printer size={18} /> Imprimir
                    </button>
                    <button className="btn btn-gold d-flex align-items-center gap-2">
                        <Download size={18} /> Exportar Excel
                    </button>
                </div>
            </div>

            {/* Grfico de Barras */}
            <Row className="mb-4">
                <Col md={12}>
                    <Card className="card-dark p-4">
                        <h4 className="card-title-dark">Balance: Ingresos vs Egresos</h4>
                        <div style={{ width: '100%', height: 350 }}>
                            <ResponsiveContainer>
                                <BarChart data={dataFinanciera}>
                                    <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#333" />
                                    <XAxis dataKey="mes" axisLine={false} tick={{fill: '#888'}} />
                                    <YAxis axisLine={false} tick={{fill: '#888'}} />
                                    <Tooltip contentStyle={{ backgroundColor: '#222', border: 'none', color: '#fff' }} />
                                    <Legend wrapperStyle={{ color: '#ccc' }} />
                                    <Bar dataKey="ingresos" fill="#D4AF37" name="Ingresos (S/)" radius={[4, 4, 0, 0]} />
                                    <Bar dataKey="egresos" fill="#df0000" stroke="#555" name="Egresos (S/)" radius={[4, 4, 0, 0]} />
                                </BarChart>
                            </ResponsiveContainer>
                        </div>
                    </Card>
                </Col>
            </Row>

            {/* Tabla de Reportes Disponibles */}
            <Row>
                <Col md={12}>
                    <Card className="card-dark p-4">
                        <h4 className="card-title-dark mb-3">Historial de Reportes Generados</h4>
                        <Table hover variant="dark" responsive className="mb-0 bg-transparent">
                            <thead>
                                <tr style={{color: '#888'}}>
                                    <th>Nombre del Reporte</th>
                                    <th>Fecha Creacin</th>
                                    <th>Generado Por</th>
                                    <th>Formato</th>
                                    <th>Accin</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td className="d-flex align-items-center gap-2">
                                        <FileText size={18} className="text-gold"/> Cierre de Caja Noviembre
                                    </td>
                                    <td>01/12/2025</td>
                                    <td>Admin</td>
                                    <td><span className="badge bg-danger">PDF</span></td>
                                    <td><button className="btn btn-sm btn-link text-gold">Descargar</button></td>
                                </tr>
                                <tr>
                                    <td className="d-flex align-items-center gap-2">
                                        <FileText size={18} className="text-gold"/> Asistencia de Personal
                                    </td>
                                    <td>15/12/2025</td>
                                    <td>RRHH</td>
                                    <td><span className="badge bg-success">XLS</span></td>
                                    <td><button className="btn btn-sm btn-link text-gold">Descargar</button></td>
                                </tr>
                            </tbody>
                        </Table>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default Reportes;