import React from 'react';
import { Container, Row, Col, Card, Badge } from 'react-bootstrap';
import { Calendar, Clock, Plus, Tag } from 'lucide-react';

const Promociones = () => {
    // Datos simulados de promociones
    const promociones = [
        { 
            id: 1, 
            titulo: "Noche de Pker VIP", 
            estado: "Activa", 
            fecha: "Viernes 14 - 22:00 hrs", 
            desc: "Torneo exclusivo para clientes Platinum con pozo acumulado.",
            color: "linear-gradient(135deg, #1e1e1e 0%, #D4AF37 100%)" // Dorado
        },
        { 
            id: 2, 
            titulo: "Sorteo 0km", 
            estado: "Activa", 
            fecha: "Sbado 15 - 20:00 hrs", 
            desc: "Por cada S/ 100 en fichas obtienes un cupn para el auto.",
            color: "linear-gradient(135deg, #111 0%, #b91c1c 100%)" // Rojo
        },
        { 
            id: 3, 
            titulo: "Buffet 2x1", 
            estado: "Programada", 
            fecha: "Domingo 16 - 13:00 hrs", 
            desc: "Almuerzo ejecutivo para socios Gold y superiores.",
            color: "linear-gradient(135deg, #111 0%, #15803d 100%)" // Verde
        },
        { 
            id: 4, 
            titulo: "Happy Hour Tragamonedas", 
            estado: "Programada", 
            fecha: "Lunes 17 - 18:00 hrs", 
            desc: "Puntos dobles en todas las mquinas de la zona A.",
            color: "linear-gradient(135deg, #111 0%, #1d4ed8 100%)" // Azul
        }
    ];

    return (
        <Container fluid className="p-4 dashboard-container">
            <div className="d-flex justify-content-between align-items-center mb-4">
                <div>
                    <h2 className="fw-bold text-white">Gestin de Promociones</h2>
                    <p className="text-muted-dark">Campaas activas y calendario de eventos</p>
                </div>
                <button className="btn btn-gold d-flex align-items-center gap-2">
                    <Plus size={18} />
                    Nueva Promocin
                </button>
            </div>

            <Row className="g-4">
                {promociones.map((promo) => (
                    <Col md={6} lg={4} key={promo.id}>
                        <Card className="card-dark h-100 overflow-hidden border-0">
                            {/* Cabecera visual simulando imagen */}
                            <div style={{ height: '120px', background: promo.color, position: 'relative' }} className="p-3 d-flex flex-column justify-content-between">
                                <div className="d-flex justify-content-between">
                                    <Badge bg={promo.estado === 'Activa' ? 'success' : 'secondary'}>
                                        {promo.estado}
                                    </Badge>
                                    <Tag color="white" size={20} style={{opacity: 0.7}}/>
                                </div>
                            </div>
                            
                            <Card.Body className="p-4">
                                <h4 className="text-white fw-bold mb-2">{promo.titulo}</h4>
                                <p className="text-muted-dark mb-4" style={{fontSize: '0.9rem'}}>
                                    {promo.desc}
                                </p>
                                
                                <div className="d-flex align-items-center gap-2 text-gold mb-2">
                                    <Calendar size={16} />
                                    <small>{promo.fecha}</small>
                                </div>
                                <div className="d-flex align-items-center gap-2 text-muted-dark">
                                    <Clock size={16} />
                                    <small>Restan 2 das</small>
                                </div>
                            </Card.Body>
                            
                            <Card.Footer className="bg-transparent border-top border-secondary p-3">
                                <button className="btn btn-sm btn-outline-light w-100">Ver Detalles</button>
                            </Card.Footer>
                        </Card>
                    </Col>
                ))}
            </Row>
        </Container>
    );
};

export default Promociones;