import React, { useState } from 'react';
import { Container, Row, Col, Card, ProgressBar, Modal, Button, Form, Badge } from 'react-bootstrap';
import { Dices, Spade, CircleDollarSign, Plus, Save, Activity, Settings, AlertTriangle, CheckCircle } from 'lucide-react';

const Juegos = () => {
    // --- ESTADOS ---
    const [showModalJuego, setShowModalJuego] = useState(false);
    const [showModalMaquina, setShowModalMaquina] = useState(false);
    
    // Estado para saber QU mquina se clicke
    const [maquinaSeleccionada, setMaquinaSeleccionada] = useState(null);

    const [juegos, setJuegos] = useState([
        { id: 1, nombre: "Ruleta Americana", rentabilidad: 85, jugadas: "12,400", icono: "ruleta", color: "bg-success" },
        { id: 2, nombre: "Blackjack VIP", rentabilidad: 60, jugadas: "8,200", icono: "cartas", color: "bg-primary" },
        { id: 3, nombre: "Pker Texas", rentabilidad: 45, jugadas: "5,100", icono: "dados", color: "bg-warning" },
    ]);

    // Simulamos datos de mquinas (Esto vendr de BD luego)
    const [maquinas, setMaquinas] = useState([
        { id: "M-101", estado: "online", recaudacion: 1500, ult_mante: "10/01/2026", modelo: "IGT Crystal" },
        { id: "M-102", estado: "online", recaudacion: 2300, ult_mante: "05/01/2026", modelo: "Aristocrat" },
        { id: "M-103", estado: "mantenimiento", recaudacion: 0, ult_mante: "Hoy", modelo: "IGT Crystal", error: "Atasco de papel" },
        { id: "M-104", estado: "online", recaudacion: 890, ult_mante: "12/12/2025", modelo: "Konami" },
        { id: "M-105", estado: "advertencia", recaudacion: 4500, ult_mante: "01/01/2026", modelo: "Aristocrat", error: "Caja llena" },
        { id: "M-106", estado: "online", recaudacion: 120, ult_mante: "09/01/2026", modelo: "Konami" },
    ]);

    // --- FUNCIONES ---
    const handleOpenMaquina = (maquina) => {
        setMaquinaSeleccionada(maquina);
        setShowModalMaquina(true);
    };

    const cambiarEstadoMaquina = (nuevoEstado) => {
        // Actualizamos la lista simulada
        const nuevasMaquinas = maquinas.map(m => 
            m.id === maquinaSeleccionada.id ? {...m, estado: nuevoEstado} : m
        );
        setMaquinas(nuevasMaquinas);
        setMaquinaSeleccionada({...maquinaSeleccionada, estado: nuevoEstado}); // Actualizamos la vista actual
    };

    const renderIcon = (tipo) => {
        switch(tipo) {
            case 'ruleta': return <CircleDollarSign size={24}/>;
            case 'cartas': return <Spade size={24}/>;
            default: return <Dices size={24}/>;
        }
    };

    // Funcin para pintar el color de la mquina segn estado
    const getStatusColor = (estado) => {
        switch(estado) {
            case 'online': return '#1a1a1a'; // Normal
            case 'mantenimiento': return '#3f1a1a'; // Rojizo
            case 'advertencia': return '#3f351a'; // Amarillento
            default: return '#1a1a1a';
        }
    };

    return (
        <Container fluid className="p-4 dashboard-container">
            {/* Cabecera */}
            <div className="d-flex justify-content-between align-items-center mb-4">
                <div>
                    <h2 className="fw-bold text-white">Catlogo de Juegos</h2>
                    <p className="text-muted-dark">Gestin de mesas y parque de mquinas</p>
                </div>
                <button className="btn btn-gold d-flex align-items-center gap-2" onClick={() => setShowModalJuego(true)}>
                    <Plus size={18} /> Nuevo Juego
                </button>
            </div>

            {/* Top Juegos */}
            <Row className="mb-4 g-4">
                {juegos.map((juego, idx) => (
                    <Col md={4} key={idx}>
                        <Card className="card-dark h-100 p-3">
                            <div className="d-flex align-items-center gap-3 mb-3">
                                <div className={`icon-box ${juego.color} bg-opacity-25 text-white`} style={{background: 'rgba(255,255,255,0.1)'}}>
                                    {renderIcon(juego.icono)}
                                </div>
                                <div>
                                    <h5 className="mb-0 text-white">{juego.nombre}</h5>
                                    <small className="text-gold">{juego.jugadas} jugadas/mes</small>
                                </div>
                            </div>
                            <div className="mt-2">
                                <div className="d-flex justify-content-between mb-1">
                                    <small className="text-muted-dark">Rentabilidad</small>
                                    <small className="text-white fw-bold">{juego.rentabilidad}%</small>
                                </div>
                                <ProgressBar variant="warning" now={juego.rentabilidad} style={{height: '6px', backgroundColor: '#333'}} />
                            </div>
                        </Card>
                    </Col>
                ))}
            </Row>

            {/* Grid de Mquinas Interactivo */}
            <Row>
                <Col md={12}>
                    <Card className="card-dark p-4">
                        <div className="d-flex justify-content-between align-items-center mb-4">
                            <h4 className="card-title-dark m-0">Monitoreo de Tragamonedas</h4>
                            <div className="d-flex gap-2">
                                <Badge bg="success" className="d-flex align-items-center gap-1"><CheckCircle size={10}/> Online</Badge>
                                <Badge bg="danger" className="d-flex align-items-center gap-1"><Settings size={10}/> Mantenimiento</Badge>
                                <Badge bg="warning" text="dark" className="d-flex align-items-center gap-1"><AlertTriangle size={10}/> Alerta</Badge>
                            </div>
                        </div>
                        
                        <div className="d-flex gap-3 flex-wrap">
                            {maquinas.map((maquina, i) => (
                                <div 
                                    key={i} 
                                    className="p-3 border border-secondary rounded text-center machine-card" 
                                    style={{
                                        minWidth: '110px', 
                                        flex: '1 0 auto', 
                                        background: getStatusColor(maquina.estado),
                                        cursor: 'pointer',
                                        transition: '0.2s'
                                    }}
                                    onClick={() => handleOpenMaquina(maquina)}
                                >
                                    <div className="mb-2">
                                        {maquina.estado === 'online' && <Activity size={20} className="text-success"/>}
                                        {maquina.estado === 'mantenimiento' && <Settings size={20} className="text-danger"/>}
                                        {maquina.estado === 'advertencia' && <AlertTriangle size={20} className="text-warning"/>}
                                    </div>
                                    <h5 className="text-white mb-1">{maquina.id}</h5>
                                    <small className={
                                        maquina.estado === 'online' ? "text-success" : 
                                        maquina.estado === 'mantenimiento' ? "text-danger" : "text-warning"
                                    }>
                                        {maquina.estado === 'online' ? 'Operativa' : maquina.estado}
                                    </small>
                                </div>
                            ))}
                        </div>
                    </Card>
                </Col>
            </Row>

            {/* === MODAL 1: NUEVO JUEGO (Ya lo tenas) === */}
            <Modal show={showModalJuego} onHide={() => setShowModalJuego(false)} centered contentClassName="bg-dark border border-secondary text-white">
                <Modal.Header closeButton closeVariant="white">
                    <Modal.Title>Registrar Nuevo Juego</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group className="mb-3">
                            <Form.Label>Nombre del Juego</Form.Label>
                            <Form.Control type="text" placeholder="Ej: Ruleta Rusa" className="bg-dark text-white border-secondary"/>
                        </Form.Group>
                        {/* ... Resto del formulario ... */}
                    </Form>
                </Modal.Body>
                <Modal.Footer className="border-secondary">
                    <Button variant="outline-secondary" onClick={() => setShowModalJuego(false)}>Cancelar</Button>
                    <Button variant="warning">Guardar</Button>
                </Modal.Footer>
            </Modal>

            {/* === MODAL 2: DETALLE DE MQUINA (NUEVO) === */}
            <Modal show={showModalMaquina} onHide={() => setShowModalMaquina(false)} centered contentClassName="bg-dark border border-secondary text-white">
                {maquinaSeleccionada && (
                    <>
                    <Modal.Header closeButton closeVariant="white" className="border-secondary">
                        <div className="d-flex align-items-center gap-2">
                            <Modal.Title>{maquinaSeleccionada.id}</Modal.Title>
                            <Badge bg="secondary">{maquinaSeleccionada.modelo}</Badge>
                        </div>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="text-center mb-4">
                            <h6 className="text-muted-dark">Recaudacin Actual (Caja)</h6>
                            <h2 className="text-gold fw-bold">S/ {maquinaSeleccionada.recaudacion.toFixed(2)}</h2>
                        </div>
                        
                        <div className="bg-black p-3 rounded mb-3 border border-secondary">
                            <div className="d-flex justify-content-between mb-2">
                                <span className="text-muted">Estado Actual:</span>
                                <span className={`fw-bold text-${maquinaSeleccionada.estado === 'online' ? 'success' : 'danger'}`}>
                                    {maquinaSeleccionada.estado.toUpperCase()}
                                </span>
                            </div>
                            <div className="d-flex justify-content-between mb-2">
                                <span className="text-muted">ltimo Mantenimiento:</span>
                                <span className="text-white">{maquinaSeleccionada.ult_mante}</span>
                            </div>
                            {maquinaSeleccionada.error && (
                                <div className="d-flex justify-content-between">
                                    <span className="text-muted">Alerta detectada:</span>
                                    <span className="text-warning fw-bold">{maquinaSeleccionada.error}</span>
                                </div>
                            )}
                        </div>

                        <div className="d-grid gap-2">
                            <p className="mb-1 text-white small">Acciones de Tcnico:</p>
                            {maquinaSeleccionada.estado === 'online' ? (
                                <Button variant="outline-danger" onClick={() => cambiarEstadoMaquina('mantenimiento')}>
                                    <Settings size={16} className="me-2"/> Reportar Falla / Mantenimiento
                                </Button>
                            ) : (
                                <Button variant="outline-success" onClick={() => cambiarEstadoMaquina('online')}>
                                    <CheckCircle size={16} className="me-2"/> Restaurar Sistema (Online)
                                </Button>
                            )}
                        </div>
                    </Modal.Body>
                    </>
                )}
            </Modal>

        </Container>
    );
};

export default Juegos;