import React, { useState, useEffect } from 'react';
import { Search, Filter, Plus, MoreHorizontal, User } from 'lucide-react';
import axios from 'axios'; 


const Clientes = () => {
    // ESTADO: Empieza vaco, esperando datos reales
    const [clientes, setClientes] = useState([]);
    const [cargando, setCargando] = useState(false);

    // EFECTO: Aqu es donde pediremos los datos a Django (BD)
    useEffect(() => {
        obtenerClientes(); 
    }, []);

    const obtenerClientes = async () => {
        try {
            setCargando(true);
            // --- CDIGO PARA CONECTAR (Descomentar luego) ---
            // const response = await axios.get('http://127.0.0.1:8000/api/clientes/');
            // setClientes(response.data);
            
            // Por ahora simulamos una carga rpida y dejamos la lista vaca
            setTimeout(() => setCargando(false), 500);
        } catch (error) {
            console.error("Error cargando clientes:", error);
            setCargando(false);
        }
    };

    // Funcin para pintar etiquetas de Nivel
    const getNivelColor = (nivel) => {
        switch(nivel) {
            case 'Platinum': return 'badge-platinum';
            case 'Gold': return 'badge-gold';
            case 'Silver': return 'badge-silver';
            default: return 'badge-default';
        }
    };

    return (
        <div className="clientes-container">
            {/* Cabecera */}
            <div className="page-header">
                <div>
                    <h1 className="page-title">Cartera de Clientes</h1>
                    <p className="page-subtitle">Gestin y seguimiento de jugadores VIP</p>
                </div>
                <button className="btn-gold">
                    <Plus size={18} />
                    Nuevo Cliente
                </button>
            </div>

            {/* Herramientas */}
            <div className="toolbar">
                <div className="search-bar">
                    <Search size={18} color="#666" />
                    <input type="text" placeholder="Buscar por nombre o DNI..." />
                </div>
                <div className="filters">
                    <button className="btn-outline">
                        <Filter size={18} />
                        Filtrar
                    </button>
                </div>
            </div>

            {/* Tabla */}
            <div className="table-container">
                <table className="clients-table">
                    <thead>
                        <tr>
                            <th>Cliente</th>
                            <th>DNI</th>
                            <th>Nivel</th>
                            <th>Email</th>
                            <th>Celular</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>
                        {/* CASO 1: CARGANDO */}
                        {cargando && (
                            <tr>
                                <td colSpan="6" style={{textAlign: 'center', padding: '40px', color: '#888'}}>
                                    Cargando datos del sistema...
                                </td>
                            </tr>
                        )}

                        {/* CASO 2: NO HAY DATOS (Se muestra esto por defecto) */}
                        {!cargando && clientes.length === 0 && (
                            <tr>
                                <td colSpan="6" style={{textAlign: 'center', padding: '60px', color: '#666'}}>
                                    <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '15px'}}>
                                        <div style={{background: '#222', padding: '15px', borderRadius: '50%'}}>
                                            <User size={30} color="#444" />
                                        </div>
                                        <span style={{fontSize: '16px'}}>No se encontraron clientes registrados.</span>
                                        <span style={{fontSize: '13px', color: '#444'}}>
                                            La interfaz est lista. Conecta la base de datos para ver informacin.
                                        </span>
                                    </div>
                                </td>
                            </tr>
                        )}

                        {/* CASO 3: SI HAY DATOS (Se activar cuando descomentes el axios) */}
                        {clientes.map((cliente) => (
                            <tr key={cliente.idcliente}>
                                <td>
                                    <div className="client-info">
                                        <div className="client-avatar">
                                            {cliente.nombres ? cliente.nombres.charAt(0) : 'U'}
                                        </div>
                                        <div>
                                            <span style={{display: 'block', fontWeight: 'bold'}}>
                                                {cliente.nombres} {cliente.apellidos}
                                            </span>
                                        </div>
                                    </div>
                                </td>
                                <td>{cliente.dni}</td>
                                <td>
                                    <span className={`badge ${getNivelColor(cliente.nivel)}`}>
                                        {cliente.nivel || 'Nuevo'}
                                    </span>
                                </td>
                                <td>{cliente.email || '-'}</td>
                                <td>{cliente.celular || '-'}</td>
                                <td>
                                    <button className="btn-icon">
                                        <MoreHorizontal size={18} />
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default Clientes;