import React, { useState } from 'react';
import { Card, Form, Button } from 'react-bootstrap'; // Quitamos 'Container' porque usaremos un div custom
import { useNavigate } from 'react-router-dom';

const Login = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        // Aqu validaremos con Django luego
        navigate('/dashboard');
    };

    return (
        // AQU EST LA CLAVE: Usamos la clase 'login-bg'
        <div className="login-bg">
        <Card className="card-glass p-4" style={{ width: '400px', borderRadius: '15px' }}>
            <Card.Body>
            <h2 className="text-center mb-4 text-white">
                ATLANTIC <span className="text-gold">CITY</span>
            </h2>
            <p className="text-center text-secondary mb-4">Gestin de Clientes CRM</p>

            <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3">
                <Form.Label className="text-gold">Usuario / Correo</Form.Label>
                <Form.Control 
                    type="email" 
                    placeholder="ejemplo@atlantic.com" 
                    className="input-dark" 
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                </Form.Group>

                <Form.Group className="mb-4">
                <Form.Label className="text-gold">Contrasea</Form.Label>
                <Form.Control 
                    type="password" 
                    placeholder="" 
                    className="input-dark"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                </Form.Group>

                <Button variant="primary" type="submit" className="w-100 btn-gold py-2">
                INICIAR SESIN
                </Button>
            </Form>
            </Card.Body>
        </Card>
        </div>
    );
};

export default Login;