import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { PieChart, Pie, Cell, Tooltip, ResponsiveContainer, Legend } from 'recharts';
import { Filter, Users, Trophy, UserPlus, UserX } from 'lucide-react';

const Segmentacion = () => {
    // Datos simulados para los grficos
    const dataSegmentos = [
        { name: 'VIP Platinum', value: 120, color: '#e5e4e2' },
        { name: 'VIP Gold', value: 300, color: '#D4AF37' },
        { name: 'VIP Silver', value: 800, color: '#71717a' },
        { name: 'Bronce', value: 650, color: '#cd7f32' },  
        { name: 'Nuevos', value: 450, color: '#3b82f6' },  
        { name: 'Inactivos', value: 200, color: '#ef4444' }, 
    ];

    return (
        <Container fluid className="p-4 dashboard-container">
            <div className="d-flex justify-content-between align-items-center mb-4">
                <div>
                    <h2 className="fw-bold text-white">Segmentacin de Clientes</h2>
                    <p className="text-muted-dark">Anlisis de grupos y comportamiento de juego</p>
                </div>
                <button className="btn btn-outline-warning d-flex align-items-center gap-2">
                    <Filter size={18} />
                    Filtrar Vista
                </button>
            </div>

            {/* Tarjetas de Resumen de Grupos */}
            <Row className="g-4 mb-4">
                <Col md={3}>
                    <Card className="card-dark h-100 text-center p-3">
                        <Card.Body>
                            <div className="icon-box bg-warning-dark mx-auto mb-3">
                                <Trophy size={24} />
                            </div>
                            <h5 className="text-white">VIP Total</h5>
                            <h2 className="fw-bold text-gold">420</h2>
                            <small className="text-muted-dark">Gold + Platinum</small>
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={3}>
                    <Card className="card-dark h-100 text-center p-3">
                        <Card.Body>
                            <div className="icon-box bg-primary-dark mx-auto mb-3">
                                <UserPlus size={24} />
                            </div>
                            <h5 className="text-white">Nuevos (Mes)</h5>
                            <h2 className="fw-bold text-white">45</h2>
                            <small className="text-muted-dark">+15% vs mes anterior</small>
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={3}>
                    <Card className="card-dark h-100 text-center p-3">
                        <Card.Body>
                            <div className="icon-box mx-auto mb-3" style={{background: 'rgba(255, 255, 255, 0.1)'}}>
                                <Users size={24} color="white" />
                            </div>
                            <h5 className="text-white">Regulares</h5>
                            <h2 className="fw-bold text-white">800</h2>
                            <small className="text-muted-dark">Visitas semanales</small>
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={3}>
                    <Card className="card-dark h-100 text-center p-3">
                        <Card.Body>
                            <div className="icon-box bg-danger-dark mx-auto mb-3">
                                <UserX size={24} />
                            </div>
                            <h5 className="text-white">En Riesgo</h5>
                            <h2 className="fw-bold text-danger">200</h2>
                            <small className="text-muted-dark">Sin visitas &gt; 30 das</small>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>

            {/* Grfico Principal */}
            <Row className="mb-4">
                <Col md={12}>
                    <Card className="card-dark p-4">
                        <h4 className="card-title-dark">Distribucin de la Base de Datos</h4>
                        <div style={{ width: '100%', height: 400 }}>
                            <ResponsiveContainer>
                                <PieChart>
                                    <Pie
                                        data={dataSegmentos}
                                        cx="50%"
                                        cy="50%"
                                        innerRadius={100}
                                        outerRadius={140}
                                        paddingAngle={5}
                                        dataKey="value"
                                        label={({name, percent}) => `${name} ${(percent * 100).toFixed(0)}%`}
                                        stroke="none" // Quita lneas blancas feas
                                    >
                                        {dataSegmentos.map((entry, index) => (
                                            <Cell key={`cell-${index}`} fill={entry.color} stroke="none" />
                                        ))}
                                    </Pie>
                                    
                                    {/* TOOLTIP CORREGIDO */}
                                    <Tooltip 
                                        contentStyle={{ 
                                            backgroundColor: '#111', 
                                            border: '1px solid #D4AF37', 
                                            borderRadius: '8px',
                                            boxShadow: '0 4px 10px rgba(0,0,0,0.5)'
                                        }} 
                                        itemStyle={{ color: '#fff', fontWeight: 'bold' }}
                                    />
                                    
                                    <Legend verticalAlign="bottom" height={36} iconType="circle" wrapperStyle={{ color: '#ccc', marginTop: '20px' }}/>
                                </PieChart>
                            </ResponsiveContainer>
                        </div>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default Segmentacion;