import React from 'react';
import { Container, Row, Col, Card, Badge, Form, InputGroup } from 'react-bootstrap';
import { Search, MessageSquare, AlertCircle, CheckCircle } from 'lucide-react';

const Atencion = () => {
    const tickets = [
        { id: "#TK-9012", cliente: "Juan Prez", asunto: "Problema con canje de puntos", estado: "Abierto", prioridad: "Alta" },
        { id: "#TK-9013", cliente: "Isabel Ramrez", asunto: "Consulta sobre horario VIP", estado: "En Proceso", prioridad: "Media" },
        { id: "#TK-9014", cliente: "Carlos Diaz", asunto: "Tarjeta no leda en mquina", estado: "Cerrado", prioridad: "Alta" },
    ];

    return (
        <Container fluid className="p-4 dashboard-container">
             <div className="d-flex justify-content-between align-items-center mb-4">
                <div>
                    <h2 className="fw-bold text-white">Centro de Ayuda</h2>
                    <p className="text-muted-dark">Gestin de tickets e incidencias</p>
                </div>
                <button className="btn btn-gold">
                    + Nuevo Ticket
                </button>
            </div>

            <Row className="g-4">
                {/* Columna Izquierda: Lista de Tickets */}
                <Col lg={8}>
                    <Card className="card-dark p-0 overflow-hidden">
                        <div className="p-3 border-bottom border-secondary d-flex justify-content-between align-items-center">
                            <h5 className="m-0 text-white">Tickets Recientes</h5>
                            <InputGroup style={{width: '250px'}} size="sm">
                                <InputGroup.Text className="bg-dark border-secondary text-white"><Search size={14}/></InputGroup.Text>
                                <Form.Control placeholder="Buscar ticket..." className="bg-dark border-secondary text-white placeholder-gray" />
                            </InputGroup>
                        </div>
                        
                        <div>
                            {tickets.map((t, i) => (
                                <div key={i} className="d-flex justify-content-between align-items-center p-3 border-bottom border-secondary hover-effect">
                                    <div className="d-flex align-items-center gap-3">
                                        <div className={`icon-box rounded-circle ${t.estado === 'Abierto' ? 'bg-danger-dark' : 'bg-success-dark'}`}>
                                            {t.estado === 'Abierto' ? <AlertCircle size={20}/> : <CheckCircle size={20}/>}
                                        </div>
                                        <div>
                                            <h6 className="text-white mb-0">{t.asunto}</h6>
                                            <small className="text-muted-dark">{t.id}  {t.cliente}</small>
                                        </div>
                                    </div>
                                    <div className="text-end">
                                        <Badge bg={t.estado === 'Abierto' ? 'danger' : t.estado === 'En Proceso' ? 'warning' : 'success'} className="mb-1 d-block">
                                            {t.estado}
                                        </Badge>
                                        <small className="text-secondary">{t.prioridad}</small>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </Card>
                </Col>

                {/* Columna Derecha: Estadsticas Rpidas */}
                <Col lg={4}>
                    <Card className="card-dark p-4 mb-4 text-center">
                        <MessageSquare size={40} className="text-gold mx-auto mb-3"/>
                        <h2 className="text-white fw-bold">5</h2>
                        <span className="text-muted-dark">Tickets Pendientes Hoy</span>
                    </Card>

                    <Card className="card-dark p-4">
                        <h5 className="text-white mb-3">Satisfaccin</h5>
                        <div className="d-flex justify-content-between align-items-end">
                            <h1 className="text-success fw-bold m-0">98%</h1>
                            <span className="text-muted-dark mb-2">Positiva</span>
                        </div>
                        <div className="progress mt-2" style={{height: '5px'}}>
                            <div className="progress-bar bg-success" style={{width: '98%'}}></div>
                        </div>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default Atencion;