    import React from 'react';
    import { Link, useLocation } from 'react-router-dom';
    import { 
        LayoutDashboard, // Para Dashboard
        Users,           // Para Clientes
        PieChart,        // Para Segmentacin
        Tag,             // Para Promociones
        BarChart2,       // Para Reportes
        Gamepad2,        // Para Juegos
        Headphones,      // Para Atencin al Cliente
        LogOut           // Para Salir
    } from 'lucide-react';

    const Sidebar = () => {
    const location = useLocation();

    return (
        <div className="sidebar">
        <div className="sidebar-logo">
            <h2>ATLANTIC <span>CRM</span></h2>
        </div>

        <nav className="sidebar-menu">
            {/* 1. Dashboard */}
            <Link to="/dashboard" className={location.pathname === '/dashboard' ? 'active' : ''}>
            <LayoutDashboard size={20} />
            <span>Dashboard</span>
            </Link>

            {/* 2. Clientes */}
            <Link to="/clientes" className={location.pathname === '/clientes' ? 'active' : ''}>
            <Users size={20} />
            <span>Clientes</span>
            </Link>

            {/* 3. Segmentacin */}
            <Link to="/segmentacion" className={location.pathname === '/segmentacion' ? 'active' : ''}>
            <PieChart size={20} />
            <span>Segmentacin</span>
            </Link>

            {/* 4. Promociones */}
            <Link to="/promociones" className={location.pathname === '/promociones' ? 'active' : ''}>
            <Tag size={20} />
            <span>Promociones</span>
            </Link>

            {/* 5. Reportes */}
            <Link to="/reportes" className={location.pathname === '/reportes' ? 'active' : ''}>
            <BarChart2 size={20} />
            <span>Reportes</span>
            </Link>
            
            {/* 6. Juegos */}
            <Link to="/juegos" className={location.pathname === '/juegos' ? 'active' : ''}>
            <Gamepad2 size={20} />
            <span>Juegos</span>
            </Link>

            {/* 7. Atencin al Cliente */}
            <Link to="/atencion" className={location.pathname === '/atencion' ? 'active' : ''}>
            <Headphones size={20} />
            <span>Atencin al Cliente</span>
            </Link>
        </nav>

        <div className="sidebar-footer">
            <Link to="/" className="logout-btn">
            <LogOut size={20} />
            <span>Salir</span>
            </Link>
        </div>
        </div>
    );
    };

    export default Sidebar;