import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { Users, DollarSign, Tag, LifeBuoy, TrendingUp } from 'lucide-react';
import { 
    LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer,
    PieChart, Pie, Cell, Legend 
} from 'recharts';

const Dashboard = () => {
    
    const dataVisitas = [
        { mes: 'Jul', visitas: 120 }, { mes: 'Ago', visitas: 160 },
        { mes: 'Sep', visitas: 140 }, { mes: 'Oct', visitas: 200 },
        { mes: 'Nov', visitas: 210 }, { mes: 'Dic', visitas: 240 },
    ];

    const dataVIP = [
        { name: 'Gold', value: 30 }, { name: 'Platinum', value: 20 },
        { name: 'Silver', value: 40 }, { name: 'Bronze', value: 10 },
    ];

    const dataTopClientes = [
        { id: 1, nombre: 'Isabel Ramrez', nivel: 'Platinum', gasto: 'S/ 52,000', inicial: 'I', bg: '#8884d8' },
        { id: 2, nombre: 'Carlos Rodrguez', nivel: 'Platinum', gasto: 'S/ 45,000', inicial: 'C', bg: '#82ca9d' },
        { id: 3, nombre: 'Ana Lpez', nivel: 'Gold', gasto: 'S/ 32,000', inicial: 'A', bg: '#fbbf24' },
        { id: 4, nombre: 'Mara Gonzlez', nivel: 'Gold', gasto: 'S/ 28,000', inicial: 'M', bg: '#ff8042' },
        { id: 5, nombre: 'Laura Fernndez', nivel: 'Silver', gasto: 'S/ 15,000', inicial: 'L', bg: '#ef4444' },
    ];

const COLORES_VIP = [
    '#D4AF37', // Gold (Dorado clsico)
    '#E0F7FA', // Platinum (Cian muy plido, casi blanco brillante - Se ve superior)
    '#71717a', // Silver (Gris zinc/acero oscuro - Se ve metlico y distinto)
    '#cd7f32'  // Bronze (Bronce rojizo)
];

    // Estilo personalizado para los ejes de los grficos (Texto Blanco)
    const axisStyle = { fill: '#888', fontSize: 12 };

    return (
        <Container fluid className="p-4 dashboard-container">
            
            <div className="mb-4">
                <h2 className="fw-bold text-white">Dashboard</h2>
                <p className="text-muted-dark">Resumen general del casino</p>
            </div>

            {/* === SECCIN 1: TARJETAS KPI === */}
            <Row className="g-4 mb-4">
                {/* Tarjeta 1 */}
                <Col xs={12} sm={6} lg={3}>
                    <Card className="card-dark h-100">
                        <Card.Body>
                            <div className="d-flex justify-content-between align-items-start mb-3">
                                <div className="icon-box bg-primary-dark">
                                    <Users size={24} />
                                </div>
                                <span className="badge bg-success bg-opacity-25 text-success">+12%</span>
                            </div>
                            <h6 className="text-muted-dark mb-1">Total Clientes</h6>
                            <h2 className="fw-bold mb-0 text-white">8</h2>
                        </Card.Body>
                    </Card>
                </Col>

                {/* Tarjeta 2 */}
                <Col xs={12} sm={6} lg={3}>
                    <Card className="card-dark h-100">
                        <Card.Body>
                            <div className="d-flex justify-content-between align-items-start mb-3">
                                <div className="icon-box bg-success-dark">
                                    <DollarSign size={24} />
                                </div>
                                <span className="badge bg-success bg-opacity-25 text-success">+8%</span>
                            </div>
                            <h6 className="text-muted-dark mb-1">Ingresos Totales</h6>
                            <h2 className="fw-bold mb-0 text-white">S/ 289.000</h2>
                        </Card.Body>
                    </Card>
                </Col>

                {/* Tarjeta 3 */}
                <Col xs={12} sm={6} lg={3}>
                    <Card className="card-dark h-100">
                        <Card.Body>
                            <div className="d-flex justify-content-between align-items-start mb-3">
                                <div className="icon-box bg-warning-dark">
                                    <Tag size={24} />
                                </div>
                                <span className="text-warning small fw-bold">2 activas</span>
                            </div>
                            <h6 className="text-muted-dark mb-1">Promociones</h6>
                            <h2 className="fw-bold mb-0 text-white">5</h2>
                        </Card.Body>
                    </Card>
                </Col>

                {/* Tarjeta 4 */}
                <Col xs={12} sm={6} lg={3}>
                    <Card className="card-dark h-100">
                        <Card.Body>
                            <div className="d-flex justify-content-between align-items-start mb-3">
                                <div className="icon-box bg-danger-dark">
                                    <LifeBuoy size={24} />
                                </div>
                                <span className="text-danger small fw-bold">3 abiertos</span>
                            </div>
                            <h6 className="text-muted-dark mb-1">Tickets Soporte</h6>
                            <h2 className="fw-bold mb-0 text-white">5</h2>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>

            {/* === SECCIN 2: GRFICOS === */}
            <Row className="g-4 mb-4">
                {/* Grfico de Lneas */}
                <Col xs={12} lg={8}>
                    <Card className="card-dark h-100 p-3">
                        <Card.Title className="card-title-dark mb-4">Visitas Mensuales</Card.Title>
                        <div style={{ width: '100%', height: 300 }}>
                            <ResponsiveContainer>
                                <LineChart data={dataVisitas}>
                                    {/* Grid oscuro */}
                                    <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#333" />
                                    {/* Ejes con texto gris claro */}
                                    <XAxis dataKey="mes" axisLine={false} tickLine={false} tick={axisStyle} />
                                    <YAxis axisLine={false} tickLine={false} tick={axisStyle} />
                                    <Tooltip contentStyle={{ backgroundColor: '#222', borderColor: '#444', color: '#fff' }} />
                                    <Line type="monotone" dataKey="visitas" stroke="#D4AF37" strokeWidth={3} dot={{ r: 4, fill: '#D4AF37', strokeWidth: 2, stroke: '#fff' }} />
                                </LineChart>
                            </ResponsiveContainer>
                        </div>
                    </Card>
                </Col>

                {/* Grfico de Pastel */}
                <Col xs={12} lg={4}>
                    <Card className="card-dark h-100 p-3">
                        <Card.Title className="card-title-dark mb-4">Distribucin VIP</Card.Title>
<div style={{ width: '100%', height: 300 }}>
    <ResponsiveContainer>
        <PieChart>
            <Pie
                data={dataVIP}
                cx="50%"
                cy="50%"
                innerRadius={60}
                outerRadius={80}
                paddingAngle={5}
                dataKey="value"
                stroke="none"
            >
                {dataVIP.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORES_VIP[index % COLORES_VIP.length]} />
                ))}
            </Pie>
            
            {/* AQU EST EL TOOLTIP CORREGIDO */}
            <Tooltip
                contentStyle={{
                    backgroundColor: '#111',
                    border: '1px solid #D4AF37',
                    borderRadius: '8px',
                    boxShadow: '0 4px 10px rgba(0,0,0,0.5)'
                }}
                itemStyle={{ color: '#fff' }}
            />
            
            <Legend verticalAlign="bottom" height={36} iconType="circle" wrapperStyle={{ color: '#ccc' }}/>
        </PieChart>
    </ResponsiveContainer>
</div>
                    </Card>
                </Col>
            </Row>

            {/* === SECCIN 3: TOP CLIENTES === */}
            <Row className="g-4 pb-5">
                <Col xs={12}>
                    <Card className="card-dark p-4">
                        <div className="d-flex justify-content-between align-items-center mb-4">
                            <h5 className="card-title-dark m-0">Top 5 Clientes por Gasto</h5>
                            <button className="btn btn-sm btn-outline-light rounded-pill px-3">Ver Todos</button>
                        </div>

                        <div className="d-flex flex-column gap-3">
                            {dataTopClientes.map((cliente) => (
                                <div key={cliente.id} className="d-flex align-items-center justify-content-between border-bottom border-secondary pb-2">
                                    <div className="d-flex align-items-center">
                                        <div 
                                            className="d-flex align-items-center justify-content-center text-white fw-bold rounded-circle me-3"
                                            style={{ width: '45px', height: '45px', backgroundColor: cliente.bg }}
                                        >
                                            {cliente.inicial}
                                        </div>
                                        <div>
                                            <h6 className="mb-0 fw-bold text-white">{cliente.nombre}</h6>
                                            <small className="text-muted-dark">{cliente.nivel}</small>
                                        </div>
                                    </div>
                                    <div className="text-end">
                                        <h5 className="mb-0 fw-bold" style={{color: '#D4AF37'}}>{cliente.gasto}</h5>
                                        <small className="text-success small d-flex align-items-center justify-content-end">
                                            <TrendingUp size={14} className="me-1"/> 12%
                                        </small>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default Dashboard;